
<template>
  <div>
    <section>
      <div class="lv_search_line_tit">
        <h2> {{ title }}</h2>
        <div class="lv_search_line">
          <div class="lv_search_line_row">
            <!-- 出发地 -->

            <div class="lv_search_line_address">
              <span class="place">出发地：{{ getInto[0].city_name }} </span>
              <span class="risklevel">
              风险等级：{{risk_level[level][level]}}
            </span>
            </div>
            <!-- 外出政策 -->
            <div class="lv_search_line_tips">
              外出政策
            </div>
            <!-- 政策内容 -->
            <div class="lv_search_line_txt">
              <p class="limit_box">
                <span class="limit_box_txt">
                  {{ getInto[0].out_summary | deletewords }}
                </span>
              </p>
            </div>
            <!-- 查看疫情 -->
            <div class="buttons">
              <van-button class="button" color="rgba(49,146,241,.1)" icon="search" type="primary">
                查看{{getInto[0].title}}疫情地图</van-button>
              <van-button class="button" color="rgba(49,146,241,.1)" icon="search" type="info">查询地区风险等级</van-button>
            </div>
          </div>
          <div class="lv_search_line_row">
            <!-- 出发地 -->
            <div class="lv_search_line_address" v-if="getInto.length == 2">
              <span class="place">到达地：{{ getInto[getInto.length -1].city_name }} </span>
              <span class="risklevel" >风险等级：{{risk_level[levels][levels]}}</span>
            </div>
            <!-- 外出政策 -->
            <div class="lv_search_line_tips">
              进入政策
            </div>
            <!-- 政策内容 -->
            <div class="lv_search_line_txt">
              <p class="limit_box">
                <span class="limit_box_txt">{{ getInto[getInto.length -1].in_summary | deletewords }}
                </span>
              </p>
              <div class="seemore" @click="seedetails">
                点击查看更多
                <span class="iconfont icon-xiayiyeqianjinchakangengduo"></span>
              </div>
            </div>
            <!-- 查看疫情 -->
            <div class="buttons">
              <van-button class="button" color="rgba(49,146,241,.1)" icon="search" type="primary">
                查看{{ getInto[getInto.length -1].title }}疫情地图 </van-button>
              <van-button class="button" color="rgba(49,146,241,.1)" icon="search" type="info">查询地区风险等级</van-button>
            </div>

          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  props: ['getInto', 'title', 'diff'],
  data() {
    return {
      risk_level: [
        { '0': '暂无',color:'#65CA18'},
        { '1': '低' ,color:'#65CA18'},
        { '2': '中' ,color:'#FF7205'},
        { '3': '高' ,color:'#891A05'},
        { '4': '部分中、高', color:'#FF7205'},
      ],
      level:0,
      levels:0,
    }
  },
  mounted() {
    this.level = this.getInto[0].level;
    this.levels = this.getInto[this.getInto.length - 1].level;

  },
  methods: {
    seedetails() {

      let Obj = JSON.stringify(this.getInto[this.getInto.length -1]);

      this.$router.push({ name: 'details', params: { content: Obj } })
    }
  },
}
</script>
