    color: #111;
    font-weight: 700;
  }

  .lv_search_line:before {
    width: 1px;
  }

  .lv_search_line:before {
    content: "";
    position: absolute;
    left: 0;
    top: 14px;
    bottom: 0;
    width: 1px;
    background: #ccc;
  }


  .lv_search_line {
    margin: 0 30px 50px;
    padding: 0 0 0 20px;
    position: relative;

    .lv_search_line_row {
      margin-bottom: 40px;

      .lv_search_line_tips {
        font-size: 26px;
        color: #ff7205;
        margin-bottom: 20px;
      }

      .lv_search_line_address {
        box-align: center;
        align-items: center;
        -ms-flex-align: center;
