            overflow: hidden;
            -o-text-overflow: ellipsis;
            text-overflow: ellipsis;
            max-height: 300px;
          }
        }

        .seemore {
          height: 57px;
          line-height: 57px;
          text-align: center;
          font-size: 26px;
          color: #666;
          margin-top: 20px;
        }
      }

      .buttons {
        margin: 20px 0 40px;

        .button {
          color: #3192f1 !important;
          height: 48px;
          border-radius: 10px;
          margin-right: 20px;
        }

      }
