<template>
  <div class="wrap wide">
    <!-- banner部分 -->
    <div class="banner wide">
      <img class="wide" src="@/assets/images/travel.png" alt="">
    </div>
    <!-- 搜索部分 -->
    <div class="covid-center wide">
      <div class="lv_search">
        <div class="lv_search_info">
          <div class="departure" @click="setOut = true">
            <span>{{ changeValue ? from : to }}</span>
            <i class="iconfont icon-xiala2"></i>
          </div>
          <div class="ic_change iconfont icon-jiantou_zuoyouqiehuan" @click="onChangePlaces">

          </div>
          <div class="destination " @click="objective = true">
            <span :class="[to ? 'color111' : 'colorCCC']">{{ changeValue ? (to ? to : '目的地') : from }}</span>
            <i class="iconfont icon-xiala2"></i>
          </div>
        </div>
      </div>
    </div>
    <div class="covid-policy wide">
      <!-- <div class="title">本地政策</div> -->
      <Trip :list="list" />
    </div>

    <!-- 弹出级联 -->
    <van-popup round v-model="setOut" position="bottom">
      <van-picker show-toolbar :columns="columns" @confirm="onConfirm" title="切换城市" />
    </van-popup>

    <van-popup round v-model="objective" position="bottom">
      <van-picker show-toolbar :columns="columns" @confirm="onConfirmTo" title="切换城市" />
    </van-popup>
  </div>
</template>

<script>

import { gwy_policy ,linkage } from '@/api/place';
import Trip from '@/view/TravelPolicy/trip/trip.vue';

export default {
  data() {
    return {
      setOut: false,
      objective: false,
      columns: [],
      region: {},
      changeValue: true,
      fromId: '110100000000',
      toId: '',
      from: '北京市',
      to: '',
      // 区分出发地与目的地
      formdiff: 0,
      todiff: 1,
      resultData: {},
      list:[]
    }
  },
  components: {
    Trip
  },
  mounted() {
    this.levelLink();
    this.getLevel()
  },
  methods: {
    async getLevel() {
      let res= await gwy_policy(this.fromId, this.toId);
      let {config , list} = res.data;
      if(this.changeValue){
        this.list = list;
      }else{
        this.list = list.reverse()
      }
    },
    // 获取城市
    async levelLink() {
      let leveData = await linkage();
      this.columns = leveData;
    },
    onChangePlaces() {
      if (!this.to) return;
      this.changeValue = !this.changeValue;
      let arrId = [this.fromId, this.toId] = [this.toId, this.fromId];
      this.fromId = arrId[0];
      this.toId = arrId[1];
      if (this.changeValue) {
        this.formdiff = 0;
        this.todiff = 1;
      } else {
        this.formdiff = 1;
        this.todiff = 0;
      }
      this.getLevel();
    },
    // 出发地选中地区
    onConfirm(picker, values) {
      this.from = picker[1]
      this.setOut = false;
      let fromId = this.columns[values[0]].children[values[1]].code;
      if (this.formdiff === 0) {
        this.fromId = fromId;
      } else {
        this.toId = fromId;
      }
      this.getLevel();
    },
    // 目的地选中地区
    onConfirmTo(picker, values) {
      this.to = picker[1];
      this.objective = false;

      let toId = this.columns[values[0]].children[values[1]].code;
      if (this.todiff === 1) {
        this.toId = toId;
      } else {
        this.fromId = toId;
      }
      this.getLevel();
    }
  },
}
</script>
<style lang="less" scoped>
.wrap {
  background-color: #FFF;

  .covid-center {
    padding: 0 30px;
    position: relative;

    .lv_search_info {
      display: flex;
      justify-content: space-around;
      align-items: center;
      height: 120px;
      background: #f8f8f8;
      border-radius: 36px;
      box-shadow: 0 0 30px rgba(0, 0, 0, .17);
      margin-top: -40px;
      position: relative;
      .ic_change {
        font-size: 40px;
        position: absolute;
      }

      .departure,
      .destination {
        span {
          font-size: 34px;
          font-weight: 700;

        }

        span.color111 {
          color: #111;
        }

        span.colorCCC {
          color: #ccc;
        }
      }
    }
  }

  .covid-policy {
    width: 714px;
    margin: 0 auto;

    .title {
      font-size: 34px;
      color: #111;
      font-weight: 700;
      margin: 30px 0;
    }
  }
}
</style>