<template>
  <div class="wrap">
    <TripDom
    :title="'行程信息'"
    :getInto="list"
    :diff="0"
    v-if="list.length !==0"
    />
    
    <TripDom
    :title="'返程信息'"
    :getInto="reverseorder"
    :diff="1"
    v-if="reverseorder.length !==0"
    />
  </div>
</template>

<script>
  import TripDom from './tripDom/tripDom.vue'
export default {
  props: {
    list: {
      type: [],
      defaule: function () {
        return []
      }
    }
  },
  data() {
    return {
      
    }
  },
  components:{
    TripDom
  },
  computed: {
    reverseorder() {
      let listbox = JSON.stringify(this.list.length === 2 ? this.list : []);
      return JSON.parse(listbox).reverse();
    }
  }, 
  mounted() { 
  }
}
</script>
